import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

import { AppState } from '../../state';
import { useBlockNumber } from '../../state/application/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

const useARTHCirculatingSupply = () => {
  const [price, setPrice] = useState<State>({
    isLoading: true,
    value: BigNumber.from(1).pow(18),
  });

  const core = useCore();
  const blockNumber = useBlockNumber();

  const supply = useSelector((state: AppState) => state.stats.updateARTHSupply);

  const fetchValue = useCallback(async () => {
    setPrice({
      isLoading: false,
      value: supply,
    });

    console.log('got from mutlicall', supply);
  }, [supply]);

  useEffect(() => {
    fetchValue().catch((err) => console.error(`Failed to fetch ARTH supply: ${err.stack}`));
  }, [blockNumber, setPrice, core, fetchValue]);

  return price;
};

export default useARTHCirculatingSupply;
