import { createReducer } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

import * as Actions from './actions';

export interface TokenState {
  updateARTHSupply?: BigNumber;
}

export const initialState: TokenState = {
  updateARTHSupply: undefined,
};

export default createReducer(
  initialState,
  (builder) =>
    builder.addCase(
      Actions.updateARTHSupply,
      (t, { payload }: { payload: Actions.UpdateARTHSupply }) => {
        t.updateARTHSupply = payload.supply;
      },
    ),
  // .addCase(
  //   Actions.updateApprovalOf,
  //   (t, { payload }: { payload: Actions.ApprovalOfType }) => {
  //     t.approvals = t.approvals || {};
  //     t.approvals[payload.token] = t.approvals[payload.token] || {};
  //     t.approvals[payload.token][payload.from] =
  //       t.approvals[payload.token][payload.from] || {};
  //     t.approvals[payload.token][payload.from][payload.to] = payload.approval;
  //   },
  // ),
);
