import { createReducer } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

import * as Actions from './actions';

export interface TokenState {
  balanceOf: {
    [token: string]: {
      [who: string]: BigNumber;
    };
  };
  approvals: {
    [token: string]: {
      [from: string]: {
        [to: string]: BigNumber;
      };
    };
  };
}

export const initialState: TokenState = {
  balanceOf: {},
  approvals: {},
};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(Actions.updateBalanceOf, (t, { payload }: { payload: Actions.BalanceOfType }) => {
      t.balanceOf = t.balanceOf || {};
      t.balanceOf[payload.token] = t.balanceOf[payload.token] || {};
      t.balanceOf[payload.token][payload.who] = payload.bal;
    })
    .addCase(
      Actions.updateApprovalOf,
      (t, { payload }: { payload: Actions.ApprovalOfType }) => {
        t.approvals = t.approvals || {};
        t.approvals[payload.token] = t.approvals[payload.token] || {};
        t.approvals[payload.token][payload.from] =
          t.approvals[payload.token][payload.from] || {};
        t.approvals[payload.token][payload.from][payload.to] = payload.approval;
      },
    ),
);
