import { Dispatch } from '@reduxjs/toolkit';

import { IMulticallInput } from '../../protocol/Mulitcall';
import { Protocol } from '../../protocol/Protocol';

import * as Actions from './actions';

export const initStats = (core: Protocol, dispatch: Dispatch, chainId: number) => {
  setTimeout(() => _initStats(core, dispatch, chainId), 100);
};

const _initStats = (core: Protocol, dispatch: Dispatch, chainId: number) => {
  core.multicall[chainId].on(`ARTH_CIRCULATING_SUPPLY`, (bal) =>
    dispatch(
      Actions.updateARTHSupply({
        supply: bal,
      }),
    ),
  );
  const addCallsArray: Array<IMulticallInput> = [];

  addCallsArray.push({
    key: `ARTH_CIRCULATING_SUPPLY`,
    target: core.tokens('ARTH', chainId).address,
    call: ['totalSupply()(uint256)'],
    convertResult: (val) => val,
  });

  core.multicall[chainId].addCalls(addCallsArray);
};
